import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			//支付方式  1支付宝  2银行卡
			payType : 0,
			//充值弹窗是否显示
			alertIsShow : false,
			//活动充值弹窗是否显示
			alertActivityShow : 0,
			bankArray: [],
			bankIndex: 0,
			//初始化数据
			payHistory : [],
			payActHistory : [],
			//商家收款信息
			zfbReceiptInfo : {},
			yhkReceiptInfo : {},
			//支付宝充值数据
			userZfb : '',
			zfbPayMoney : '',
			//银行卡充值数据
			bankCard : '',
			bankMoney : '',
			//支付宝。银行卡转账信息
			zfbRemark : '',
			yhkRemark : '',
			//是否是继续充值
			isGoOnpay : false,
			//继续充值状态 0 普通充值  1 2 3
			isGoOnpayType : ""
		};
	},
	onLoad() {
		this.initData();
	},
	computed:{
		webPath(){
			return this.$store.state.webPath;
		}
	},
	methods: {
		initData(){
			this.isGoOnpayType = "";
			this.isGoOnpay = false;
			uni.showLoading({
				title : '',
				mask:true
			})
			http.get('transaction/torecharge',{phone:this.$store.state.userInfo.phone}).then((res)=>{
				this.payHistory = res.data.data.rechargeRecord;
				this.payActHistory = res.data.data.activityRecharge;
				let _this = this;
				res.data.data.receiptAccount.forEach((el)=>{
					if(el.type == 1002){
						_this.zfbReceiptInfo = el
					}else if(el.type == 1001){
						_this.yhkReceiptInfo = el
					}
				})
			})
		},
		bankPickerChange: function(e) {
			console.log('bankPickerChange发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		changePayNum(type,num){
			this.alertActivityShow = 1;
			if(type == '1'){
				this.zfbPayMoney = num;
			}else{
				this.bankMoney = num;
			}
			
		},
		//提交转账申请
		subApply(){
			if(this.payType == 1){
				if(!this.zfbReceiptInfo.account){
					uni.showModal({
						title : '提示',
						content : '暂不支持支付宝充值',
						showCancel : false,
					})
					return;
				}
				if(this.userZfb == ''){
					uni.showModal({
						title : '提示',
						content : '支付宝账号不能为空',
						showCancel : false,
					})
					return;
				}
				if(this.zfbPayMoney == ''){
					uni.showModal({
						title : '提示',
						content : '转账金额不能为空',
						showCancel : false,
					})
					return;
				}
				uni.showLoading({
					title : '提交中~'
				})
				
				let actType = '';
				actType = this.zfbPayMoney == '10000' ? 1 : this.zfbPayMoney == '20000' ? 2 : this.zfbPayMoney == '30000' ? 3 : '';
				//活动
				if(actType != '' && this.alertActivityShow != 0){
					// 活动充值
					http.get('transaction/activityRecharge',{
						holder:this.$store.state.userInfo.phone,
						type:'1001',
						cardNo:this.userZfb,
						amount:this.zfbPayMoney,
						activity:actType,
						recepitCardNo:this.zfbReceiptInfo.account
					}).then((res)=>{
						this.zfbRemark = res.data.data;
						this.alertIsShow = true;
						this.initData();
					})
				}else{
					// 普通充值
					http.get('transaction/rechargeOne',{
						holder:this.$store.state.userInfo.phone,
						type:'1001',
						cardNo:this.userZfb,
						amount:this.zfbPayMoney,
						recepitCardNo:this.zfbReceiptInfo.account
					}).then((res)=>{
						this.zfbRemark = res.data.data;
						this.alertIsShow = true;
						this.initData();
					})
				}
				
				
			}else if(this.payType == 2){
				if(!this.yhkReceiptInfo.account){
					uni.showModal({
						title : '提示',
						content : '暂不支持银行卡充值',
						showCancel : false,
					})
					return;
				}
				if(this.bankCard == ''){
					uni.showModal({
						title : '提示',
						content : '银行卡号不能为空',
						showCancel : false,
					})
					return;
				}
				if(this.bankMoney == ''){
					uni.showModal({
						title : '提示',
						content : '转账金额不能为空',
						showCancel : false,
					})
					return;
				}
				uni.showLoading({
					title : '提交中~'
				})
				let actType = '';
				actType = this.bankMoney == '10000' ? 1 : this.bankMoney == '20000' ? 2 : this.bankMoney == '30000' ? 3 : '';
				
				if(actType != '' && this.alertActivityShow != 0){
					
					http.get('transaction/activityRecharge',{
						holder:this.$store.state.userInfo.phone,
						type:'1002',
						cardNo:this.bankCard,
						amount:this.bankMoney,
						activity:actType,
						recepitCardNo:this.yhkReceiptInfo.account
					}).then((res)=>{
						this.yhkRemark = res.data.data;
						this.alertIsShow = true;
						this.initData();
					})
				}else{
					http.get('transaction/rechargeOne',{
						holder:this.$store.state.userInfo.phone,
						type:'1002',
						cardNo:this.bankCard,
						amount:this.bankMoney,
						recepitCardNo:this.yhkReceiptInfo.account
					}).then((res)=>{
						this.yhkRemark = res.data.data;
						this.alertIsShow = true;
						this.initData();
					})
				}
				
				
			}
		},
		//继续充值
		continuePay(type,el){
			// let eType = '';
			// if(type == '1'){
			// 	eType = el.state;
			// }else{
			// 	eType = el.type;
			// }
			if(el.type == 1001){
				this.payType = 1;
				this.zfbPayMoney = el.amount;
				this.zfbRemark = {
					'remarkCode' : el.remarkCode,
					'id' : el.id
				}
			}else if(el.type == 1002){
				this.payType = 2;
				this.bankMoney = el.amount;
				this.yhkRemark = {
					'remarkCode' : el.remarkCode,
					'id' : el.id
				}
			}
			this.isGoOnpayType = el.activity;
			this.isGoOnpay = true;
			this.alertIsShow = true;
		},
		//用户已转账  或者取消
		userTransfer(isFalse){
			if(!isFalse){
				this.alertIsShow = false;
				// this.resetPayInfo();
				return;
			}
			
			let _this = this;
			uni.showModal({
				title : '提示',
				content : '请确认是否已经转账',
				success(res) {
					if(res.confirm){
						uni.showLoading({
							title : '提交中~'
						})
						let actType = '';
						if(_this.payType == 1){
							actType = _this.zfbPayMoney == '10000' ? 1 : _this.zfbPayMoney == '20000' ? 2 : _this.zfbPayMoney == '30000' ? 3 : '';
							if(_this.isGoOnpay){
								actType = _this.isGoOnpayType;
							}
							let datas = {};
							datas.id = _this.zfbRemark.id;
							if(actType != '0'){
								datas.activity = actType
							}
							http.get('transaction/rechargeTwo',datas).then((res)=>{
								uni.showModal({
									title : '提示',
									content : '支付宝确认充值申请成功',
									showCancel : false,
								})
								_this.alertIsShow = false;
								_this.initData();
								_this.resetPayInfo();
							})
						}else if(_this.payType == 2){
							actType = _this.bankMoney == '10000' ? 1 : _this.bankMoney == '20000' ? 2 : _this.bankMoney == '30000' ? 3 : '';
							if(_this.isGoOnpay){
								actType = _this.isGoOnpayType;
							}
							let datas = {};
							datas.id = _this.yhkRemark.id;
							if(actType != '0'){
								datas.activity = actType
							}
							http.get('transaction/rechargeTwo',datas).then((res)=>{
								uni.showModal({
									title : '提示',
									content : '银行卡确认充值申请成功',
									showCancel : false,
								})
								_this.alertIsShow = false;
								_this.initData();
								_this.resetPayInfo();
							})
						}
					}
				}
			})
		},
		//保存收款二维码
		uploadImg(){
			let _this = this;
			uni.saveImageToPhotosAlbum({
				filePath : _this.webPath + 'file/' + _this.zfbReceiptInfo.openBank,
				success : ()=> {
					uni.showToast({
						title: '已保存',
						position: 'bottom',
						icon: 'none',
						duration: 2500
					})
				}
			})
		},
		//重置信息
		resetPayInfo(){
			this.payType = 0;
			this.userZfb = '';
			this.zfbPayMoney = '';
			this.zfbRemark = '';
			this.yhkRemark = '';
			this.bankCard = '';
			this.bankMoney = '';
			this.alertActivityShow = 0;
		}
	},
	filters: {
　　　　 timeZhuan(value) {
			 var date = new Date(value);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			   var Y = date.getFullYear() + '-';
			   var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
			   var D = (date.getDate() < 10 ? '0'+date.getDate() : date.getDate()) + ' ';
			   var h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
			   var m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
			   var s = (date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds());
			   return Y+M+D;
　　　　 }
　　}
}
